@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

:root {
  --primary-color: #a35df6;
  --primary-light: #252d47;
  --primary-dark: #1a1f38;
  --secondary-color: #252d47;
  --grey-color: #eee;
  --white-color: #fefefe;
  --base-height: 50px;
  --error-color: #b3255d;
  --verygood: #49c0b7;
  --good: #4abc6a;
  --average: #c1ba7f;
  --poor: #c65b82; }

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.35);
  font-weight: 400; }

::-moz-placeholder {
  color: rgba(255, 255, 255, 0.35);
  font-weight: 400; }

:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.35);
  font-weight: 400; }

:-moz-placeholder {
  color: rgba(255, 255, 255, 0.35);
  font-weight: 400; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #383346 inset;
  -webkit-text-fill-color: white !important; }

body {
  position: relative;
  font-family: 'Assistant', sans-serif;
  background: var(--primary-dark);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; }

p {
  font-size: 18px;
  line-height: 1.8; }

table {
  max-width: 840px;
  margin: 0 auto; }
  table h4 {
    margin: 0;
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2px;
    min-height: 40px;
    color: rgba(255, 255, 255, 0.6); }

table tr {
  display: flex;
  flex-wrap: wrap; }

table td {
  flex: 1 0 120px;
  padding: 20px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

table td span:first-of-type {
  font-size: 25px; }

table td span:last-of-type {
  font-size: 20px;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.6); }

.menu {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 30px auto;
  padding: 0; }

.menu a {
  display: block;
  height: 50px;
  padding: 8px 12px;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 700;
  font-size: 28px; }
  .menu a span {
    display: block;
    height: 0px;
    overflow: hidden;
    transition: 1s 2s ease-in-out; }

.menu.is-active a span {
  height: 50px; }

.page {
  width: 90vw;
  max-width: 90vw;
  height: 80vh;
  margin-top: 10vh;
  opacity: 1;
  pointer-events: auto; }
  .page__title {
    font-weight: 300;
    font-size: 60px;
    margin-top: 0; }
    .page__title a {
      position: relative;
      text-decoration: none;
      color: var(--white-color);
      text-shadow: 0 0 40px rgba(0, 0, 0, 0.35); }
  .page__container {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80vh;
    background: var(--primary-light);
    padding: 30px;
    box-shadow: 3px 3px 40px 0 rgba(0, 0, 0, 0.3); }

.page__content {
  text-align: center;
  width: 100%;
  max-width: 500px;
  opacity: 1;
  transition: all 0.2s ease-in-out; }

/*/ Report sent /*/
.has-report .page {
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease-in-out; }

.has-report .report {
  opacity: 1;
  pointer-events: auto;
  left: 5vw;
  bottom: 10vh; }

.is-sending .page__content {
  opacity: 0; }

.is-sending .loading {
  opacity: 1;
  pointer-events: auto; }

form {
  position: relative; }

.error {
  display: none; }

.form__button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  max-width: var(--base-height);
  border: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; }
  .form__button:focus {
    outline: none; }
  .form__button svg {
    width: 40%;
    height: 40%; }

.input {
  display: block;
  width: 100%;
  background: linear-gradient(to right, #b41cfe 0%, #fe8ba7 100%);
  border-radius: 30px; }

[type=color],
[type=date],
[type=datetime-local],
[type=datetime],
[type=email],
[type=month],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=time],
[type=url],
[type=week],
textarea,
select {
  height: var(--base-height);
  width: calc(100% - 2px);
  border: 1px solid transparent;
  background: var(--primary-light);
  color: var(--white-color);
  padding-left: 20px;
  margin: 1px;
  border-radius: 25px; }
  [type=color]:focus,
  [type=date]:focus,
  [type=datetime-local]:focus,
  [type=datetime]:focus,
  [type=email]:focus,
  [type=month]:focus,
  [type=number]:focus,
  [type=password]:focus,
  [type=search]:focus,
  [type=tel]:focus,
  [type=text]:focus,
  [type=time]:focus,
  [type=url]:focus,
  [type=week]:focus,
  textarea:focus,
  select:focus {
    outline: none; }

.error {
  position: absolute;
  top: 115%;
  left: 10px;
  font-size: 14px;
  color: var(--error-color); }

.status {
  position: relative;
  font-weight: 700;
  font-size: 14px; }
  .status::before {
    content: 'ðŸ‘'; }
  .status--ready::before {
    content: 'ðŸ’ª'; }

.report {
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease-in-out;
  position: fixed;
  top: 0;
  z-index: 99;
  overflow: scroll;
  width: 90vw;
  max-width: 90vw;
  margin-top: 10vh;
  background: var(--primary-light);
  box-shadow: 3px 3px 40px 0 rgba(0, 0, 0, 0.3); }

.report__header {
  padding: 3px 40px;
  background: #ffffff17;
  color: #949cb5; }
  .report__header h3 {
    font-size: 22px;
    font-weight: 300; }
  .report__header h3 span {
    color: #ffffffab; }

.report__container {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin: 20px; }

.report__footer {
  padding: 30px 0; }

.report__button {
  color: var(--white-color);
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  margin: 0 15px; }

.graph {
  position: relative;
  grid-column: 1/3;
  height: 400px;
  max-height: 400px; }
  .graph canvas {
    width: 100%;
    max-width: 100%;
    max-height: 100%; }

.panel {
  background: #0000001a; }

.panel > p {
  padding: 0 20px;
  color: #8c909a;
  font-size: 14px; }

.panel--alert {
  grid-column: 1/3;
  display: none; }

.panel__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin: 0 0 10px;
  padding: 12px 20px;
  background: #0000001a; }
  .panel__header h4 {
    font-weight: 300;
    color: #ffffffb5;
    margin: 0; }

.score {
  color: #c65a81;
  font-weight: bold;
  background: #252e47;
  text-align: center;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  line-height: 50px;
  font-size: 22px; }

.filter {
  height: 100%;
  margin-left: auto;
  margin-right: 20px;
  display: flex;
  align-items: center; }

.filter__button {
  background: none;
  border: 1px solid #ffffff38;
  border-radius: 11px;
  margin-left: 12px;
  text-transform: uppercase;
  font-size: 12px;
  padding: 6px 12px;
  color: #ffffff7d; }
  .filter__button:hover {
    background: linear-gradient(to right, #b41cfe 0%, #fe8ba7 100%);
    color: var(--white-color); }
  .filter__button:focus {
    outline: none; }

.show-fail .audit__item--pass {
  display: none; }

.show-fail .filter__button--fail {
  background: linear-gradient(to right, #b41cfe 0%, #fe8ba7 100%); }

.show-pass .audit__item--fail {
  display: none; }

.show-pass .filter__button--pass {
  background: linear-gradient(to right, #b41cfe 0%, #fe8ba7 100%); }

.footer {
  min-height: 10vh;
  padding: 30px 0;
  opacity: 0.3; }

.loading {
  position: absolute;
  z-index: 66;
  top: 30%;
  left: 0;
  width: 100%;
  text-align: center;
  opacity: 0;
  height: 0;
  transition: opacity 0.8s 0s;
  pointer-events: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }
  .loading h5 {
    flex: 0 0 100%;
    color: var(--white-color);
    font-size: 26px;
    font-weight: 300; }
  .loading h5 span {
    display: block;
    margin: 10px 0;
    opacity: 0.4;
    font-size: 14px; }

.pulse {
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 25px;
  background: linear-gradient(to right, #b41cfe 0%, #fe8ba7 100%);
  outline: 1px solid transparent;
  animation: pulseanim 1.2s ease-in-out infinite; }

@keyframes pulseanim {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1);
    opacity: 0; } }

.dialog {
  max-width: 600px;
  border: none;
  box-shadow: 0px 0px 20px 0px #00000078;
  text-align: center; }
  .dialog h4 {
    color: var(--error-color);
    font-size: 28px;
    margin-top: 0; }
  .dialog menu {
    padding-left: 0; }

.dialog__button {
  background: #261f33;
  color: white;
  border: none;
  padding: 12px 25px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700; }

.dialog__content strong {
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 80%;
  opacity: 0.8; }

.audit {
  max-height: 350px;
  margin: 20px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column; }

[class^="audit__item"] {
  position: relative;
  margin: 0 0 15px;
  padding: 10px; }
  [class^="audit__item"]:focus {
    outline: none; }
  [class^="audit__item"][open] {
    background: #ffffff08; }
  [class^="audit__item"] .summary:focus {
    outline: none; }
  [class^="audit__item"] .summary + p {
    color: #ffffffd1; }
  [class^="audit__item"] a {
    color: #48c1b8; }
  [class^="audit__item"]:hover .summary p {
    color: var(--white-color); }

.audit__item--pass .summary {
  color: var(--verygood); }

.audit__item--fail .summary {
  color: var(--poor); }

.audit__score {
  position: absolute;
  top: 15px;
  right: 15px;
  color: white;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 16px; }

.summary {
  display: flex;
  align-items: center; }
  .summary p {
    transition: all 0.2s ease;
    font-size: 16px;
    margin: 0;
    color: #ffffff63; }
